.login--form {
  margin: auto;
  display: flex;
  flex-direction: column;
  min-height: 25rem;
  max-height: auto;
  min-width: auto;
  max-width: 30rem;
  background-color: rgb(148, 105, 187);
  padding: 2rem;
  border-radius: 1rem;
}

.login--form--header {
  align-self: center;
  font-size: 2rem;
}

.login--email--field,
.login--password--field {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0 1rem 0;
}

.login--email--field > input,
.login--password--field > input {
  height: 3rem;
  border-radius: 0.3rem;
  border: none;
  width: 100%;
  padding: 1rem;
}

.login--btn {
  display: inline-block;
  background-color: rgb(37, 37, 214);
  border: none;
  border-radius: 0.4rem;
  font-size: 1.3rem;
  font-weight: bold;
  color: white;
  height: 3rem;
  margin: 1rem 0 0 0;
  cursor: pointer;
}

.login--btn:hover {
  background-color: rgb(29, 29, 167);
  border: 0.1rem outset rgb(4, 4, 90);
}

.login--sign--up--btn {
  display: inline-block;
  background-color: rgb(14, 112, 112);
  border: none;
  border-radius: 0.4rem;
  font-size: 1.3rem;
  font-weight: bold;
  color: white;
  height: 3rem;
  margin: 1rem 0 0 0;
  cursor: pointer;
}

.login--sign--up--btn:hover {
  background-color: rgb(11, 80, 80);
  border: 0.1rem outset rgb(7, 70, 70);
}
